import React, { Component } from 'react';
import { View, Dimensions, Platform, AsyncStorage } from 'react-native';
import * as Native from 'react-native';
import { TextField, Heading, Icon, Button, Dialog } from 'material-bread';
import * as MaterialComponents from 'material-bread';
import { sha256 } from 'js-sha256';
import PouchDB from './PouchDB';
import Router from './Router';
import XMLParser from 'react-native-dom-parser';

window.React = React;
window.PouchDB = PouchDB;
window.session_db = new PouchDB('session');

function digest(text) {
  return sha256(text);
}

const initLocalStorage = (async () => {
  if (Platform.OS === 'web') return;
  const localStorage = {};
  await Promise.all(['rapyd_home_view'].map(async (key) => (localStorage[key] = await AsyncStorage.getItem(key))));
  window.localStorage = localStorage;
})();

export default class extends React.Component {
  constructor(props) {
    super(props);
    window.renderApp = this.renderApp.bind(this);
    this.state = {
      login: '',
      password: '',
      url: '',
      settings_popup: false,
      loaded: false
    }
    if (Platform.OS === 'web') this.state.url = ((href) => href.substring(0, href.lastIndexOf('/')) + '/')(window.location.origin + window.location.pathname);
    Dimensions.addEventListener('change', () => this.setState({width: Math.min(732, Math.max(Dimensions.get('window').width * 65/100, 350))}))
  }

  clickEvent(event) {
    event.preventDefault();
    if (event.keyCode === 13) {
      this.loginApp({});
    };
  }

  async componentDidMount() {
    let session = window.session_db.get('session');
    const url = (await AsyncStorage.getItem('rapyd_server_url') || this.state.url);
    this.state.url = url;
    try {
      return this.loginApp(await session);
    }
    catch (error) {
      console.log(error)
    }
    if (Platform.OS === 'web') document.body.addEventListener('keyup', this.clickEvent.bind(this));
    return this.setState({url, loaded: true})
  }

  async updateSession(session) {
    if (!session._id) session._id = 'session';
    let old_session = {};
    try {
      old_session = await window.session_db.get('session')
    }
    catch (error) {}
    if (old_session._rev) session._rev = old_session._rev;
    return window.session_db.put(session);
  }

  async loginApp(session) {
    await initLocalStorage;
    const login = session.login || await digest(this.state.login);
    const password = session.password || await digest(login + this.state.password);
    session.login = login, session.password = password;
    let path = `/api/login?login=${login}&password=${password}`
    if (session && session.client_js_time) path += '&client_js_time=' + session.client_js_time;
    try {
      const response = await fetch(this.state.url + path);
      const new_session = await response.json();
      if (new_session.status === 'denied') return alert('Username/Password wrong, relogin') || this.setState({loaded: true, ClientApp: null})
      if (new_session.status !== 'success') alert('Network Error')
      else {
        session = {...session, ...new_session};
      }
    }
    catch (error) {
      console.log(error);
      alert('Network Error')
    }
    if (!session.client_js) return;
    const orm = {};
    if (Platform.OS !== 'web') new (Function.prototype.bind.apply(Function, [null, 'export_object', session.client_js]))()(orm);
    else {
      const blob = new Blob([session.client_js], {type: 'text/javascript'});
      window.export_object = orm;
      const script = document.createElement('script');
      const loaded = new Promise((resolve, reject) => (script.onload = resolve) && (script.onerror = reject));
      script.src = URL.createObjectURL(blob);
      document.head.appendChild(script);
      await loaded;
      delete window.export_object;
    }
    orm.tools.client = {components: {...MaterialComponents, ...Native, ...Router}, Native, MaterialComponents, ReactRouter: Router, XMLParser};
    //window.tools = orm.tools;
    await orm.tools.init_client();
    return this.updateSession(session);
  }

  renderApp(App) {
    console.log(App);
    return this.setState({ClientApp: <App/>})
  }

  render() {
    if (this.state.ClientApp) return this.state.ClientApp;
    if (!this.state.loaded) return <View/>;
    const width = this.state.width || Math.min(732, Math.max(Dimensions.get('window').width * 65/100, 350));
    return (
    
      <View style={{alignItems: 'center', marginTop: 34}}>

        <Heading type={3} text='Welcome'/>

        <TextField
          containerStyle={{marginTop: 20}}
          style={{fontSize: 17, marginBottom: 10, width: width}}
          label={'Username'}
          leadingIcon={
            <Icon name={'account-circle'} size={24} color={'#6e6e6e'} />
          }
          type={'flat'}
          value={this.state.login}
          onChangeText={value => this.setState({ login: value})}
        />

        <TextField
          secureTextEntry={true}
          password={Platform.OS !== 'web' ? true : undefined}
          containerStyle={{marginTop: 10}}
          style={{fontSize: 17, marginBottom: 10, width: width}}
          label={'Password'}
          leadingIcon={
            <Icon name={'lock'} size={24} color={'#6e6e6e'} />
          }
          type={'flat'}
          value={this.state.password}
          onChangeText={value => this.setState({ password: value})}
        />
        <View style={{marginTop: 10, flexDirection: 'row'}}>
          <Button text='Login' onPress={() => this.loginApp({})}/>
          <Button text='Settings' style={{marginLeft: (width) - 150}} onPress={() => this.setState({settings_popup: true})}/>
        </View>

        <Dialog visible={this.state.settings_popup}
          onTouchOutside={() => this.setState({ settings_popup: false })}
          title={'Server URL'}
          actionItems={[{text: 'Cancel', onPress: () => this.setState({settings_popup: false})}, {text: 'OK', onPress: () => AsyncStorage.setItem('rapyd_server_url', this.state.url) && this.setState({settings_popup: false})}]}>
          <TextField type='flat' value={this.state.url} onChangeText={value => this.setState({ url: value})} containerStyle={{marginTop: -30, marginBottom: 10}} style={{fontSize: 17, marginBottom: 10, width: 250}}/>
        </Dialog>

      </View>
    
    );
  
  }

}

